export const enum SizeTypes {
   Volume = "volume",
   Weight = "weight",
}

export const enum ProductTypes {
   All = "",
   BodyCare = "  ",
   HandCare = "  ",
   FeetCare = "  ",
   FaceCare = "  ",
   HairCare = "  ",
   TanningProducts = "  ",
   ShavingProducts = "  ",
   GiftSets = " ",
   HygieneProducts = " ",
   OralHygiene = "  ",
   PaperProducts = " "
}

export interface IProduct {
   img: string;
   name: string;
   sizeType: SizeTypes;
   size: number;
   barcode: number;
   manufacturer: string;
   brand: string;
   description: string;
   price: number;
   types: ProductTypes[];
}


export interface ProductState {
   products: IProduct[],
   filteredProducts: IProduct[]
}

export enum ProductActionTypes {
   GET_PRODUCTS = 'GET_PRODUCTS',
   FILTER_BY_CATEGORY = 'FILTER_BY_CATEGORY'
}


interface GetProductAction {
   type: ProductActionTypes.GET_PRODUCTS;
   payload: IProduct[];
}

interface FilterByCategoryAction {
   type: ProductActionTypes.FILTER_BY_CATEGORY;
   payload: ProductTypes;
}

export type ProductAction = GetProductAction | FilterByCategoryAction






export type QueryAction = FilterByCategoryAction